div#eafit-shortcodes-content textarea {
  width: 100% !important;
  border-radius: 5px;
}
div#eafit-shortcodes-content .preview-content {
  margin-bottom: 15px;
}
div#eafit-shortcodes-content select {
  width: 100% !important;
  border-radius: 5px;
}
div#eafit-shortcodes-content .get-code-shortcode {
  overflow: hidden;
}
div#eafit-shortcodes-content .get-code-shortcode code {
  width: 100%;
  display: block;
  text-align: center;
  padding: 5px 10px;
  background-color: #2271b1;
  color: white;
}
div#eafit-shortcodes-content p.description {
  font-size: 14px;
  font-weight: 600;
}